<template>
    <section class="py-5 text-center container">
        <div class="row py-5">
         <div class="col-lg-12 mx-auto">
           <div class="text-white p-5 shadow-sm rounded banner">
              <input class="form-control mr-sm-2 custombar" type="search" placeholder="아이디를 입력해주세요." aria-label="Search" v-model="user_email" v-on:keyup.enter="travelSearch" style="margin-left:1rem;">
             <button class="btn btn-outline-success my-2 my-sm-0" style="margin-left:1rem;" @click="travelSearch" >Search</button>
           </div>
         </div>
       </div>
     </section>
    
     <div class="container">
     <div class="row py-5">
       <div class="mb-3">
                   <h5 class="card-title">목록 <span class="text-muted fw-normal ms-2">{{startPage}} to {{endPage}}  of {{totalList}}</span></h5>
               </div>
       <!-- For demo purpose -->
   
       <!-- End -->
   
       <div class="row">
           <div class="col-lg-12">
               <div class="">
                   <div class="table-responsive">
                       <table class="table project-list-table table-nowrap align-middle table-borderless">
                           <thead>
                               <tr>
                                   <th scope="col">ID</th>
                                   <th scope="col">이름</th>
                                   <th scope="col">여행사</th>
                                   <th scope="col">이메일 인증</th>
                                   <th scope="col">권한 인증</th>
                                   <th scope="col">권한 체크</th>
                               </tr>
                           </thead>
                           <tbody>
                               <!-- 조회수로 도시명 색상 변경-->
                               <tr v-for="(item,index) of userList"   :key="index">
                            
                                   <td>{{item.email}}</td>
                                   <td>{{item.name}}</td>
                                   <td>{{item.travelAgencyName}}</td>
                                   <td>
                                       <span v-if="item.emailAuth=='미 인증'" class="badge badge-soft-danger mb-0">
                                       {{item.emailAuth}}
                                       </span>
                                       <span v-if="item.emailAuth=='인증'" class="badge badge-soft-info mb-0">
                                       {{item.emailAuth}}
                                       </span>
                                   </td>
                                   <td>
                                       <span v-if="item.authChk=='미 인증'" class="badge badge-soft-danger mb-0">
                                       {{item.authChk}}
                                       </span>
                                       <span v-if="item.authChk=='인증'" class="badge badge-soft-info mb-0">
                                       {{item.authChk}}
                                       </span>
                                   </td>
                                   <td>
                                       <ul class="list-inline mb-0">
                                           <li class="list-inline-item">
                                               <a  @click="acceptClick(item.id,item.authChk)" data-bs-toggle="tooltip" data-bs-placement="top" title="Delete" class="px-2 text-danger" style="cursor:pointer"><i class="bx bx-check font-size-18"></i></a>
                                           </li>
                                           <li class="list-inline-item dropdown">
                                               <a class="text-muted dropdown-toggle font-size-18 px-2" href="#" role="button" data-bs-toggle="dropdown" aria-haspopup="true"><i class="bx bx-dots-vertical-rounded"></i></a>
                                               <div class="dropdown-menu dropdown-menu-end" >
                                                <a class="dropdown-item" @click="acceptClick(item.id,item.authChk)" style="cursor: pointer;">확인</a><a class="dropdown-item" @click="reDeleteClick(item.id,item.authChk)" >취소</a>
                                                  </div>
                                           </li>
                                       </ul>
                                   </td>
                               </tr>
                           </tbody>
                       </table>
                   </div>
               </div>
           </div>
       </div>
        <BlackBg v-if="loading"></BlackBg>
       <Pagination v-if="pageChk" :pageListItem="pageListItem" @pageCurrent="pageCurr" :pageTotal="pageTotal"></Pagination>
     </div>
   </div>
   </template>
   
   <script>
   import Pagination from '../layout/Pagination';
   import BlackBg from "../loading/BlackBg"
   
   export default {
       data: function () {
       return {
         pageListItem : 10 ,
         page : 0 ,
         travel_title : "" ,
         pageTotal : 0 ,
         pageChk : false ,
         userList : [] ,
         loading:false,
         search_type : "TITLE",
         date_type:"CREATEDAT",
         startDate:'',
         startDateSplit:'',
         endDate:'',
         endDateSplit:'',
         adminAuth:'',
         startPage:'',
         endPage:'',
         totalList:'',
         user_email:'',

       }
     },
     components: {
       Pagination,
       BlackBg
     },
     created() {
   
         this.init();
     },
   
     methods: {
       init() {
         this.dateInit();
           this.adminList();
           this.authChk();
       },
       authChk() {
         const headers = { 
           'Authorization': 'Bearer ' + sessionStorage.getItem("token")
         }
         this.$axios.get(process.env.VUE_APP_AUTH,{headers}).then((res) =>{
             if(res.data.resultCode=="SUCCESS"){
               this.adminAuth = res.data.result.role;
             }
           }).catch(() => {
                this.$swal('','잠시후 다시 이용해주세요.','error');
           }).finally(() => {
           });
       },
       dateInit(){
           let myDate = new Date();
           let yy = String(myDate.getFullYear());
           let mm = String(myDate.getMonth()+1 < 10 ? '0' + myDate.getMonth()+1 : myDate.getMonth()+1);
           let dd = String(myDate.getDate() < 10 ? '0' + myDate.getDate() : myDate.getDate());
           this.startDate=  yy + '-' + mm + '-' + "01";
           this.startDateSplit = yy+mm+"01";
           this.endDate= yy + '-' + mm + '-' + dd;
           this.endDateSplit = yy+mm+dd;
       },  
   
       adminList() {
         const headers = {
               'Authorization': 'Bearer ' + sessionStorage.getItem("token")
           }
         let parameter = {
           "page" : this.page,
            "searchKeyWord" :this.user_email
         }
         this.loading = true;
         this.$axios.get(process.env.VUE_APP_AUTH_LIST,{headers,params:parameter}).then((res) =>{
            if(res.data.resultCode=="SUCCESS"){
             this.pageTotal = res.data.result.totalElements;
   
             this.totalList =res.data.result.totalElements;
             this.startPage = (res.data.result.number*res.data.result.size)+1;
             if(res.data.result.numberOfElements==res.data.result.size){
                 this.endPage = (res.data.result.number+1)*res.data.result.size;
             } else {
                 this.endPage = (res.data.result.number*res.data.result.size)+res.data.result.numberOfElements;
             }
               this.userList=[];
               res.data.result.content.forEach(element => {
                   let obj = [];
                   obj.email         = element.email;
                   obj.name          = element.name;
                   obj.id            = element.id;
                   obj.travelAgencyName = element.travelAgencyName;
                   if(element.emailAuth){
                        obj.emailAuth          = '인증';
                   } else {
                        obj.emailAuth          = '미 인증';
                   }
                   if(element.authChk){
                        obj.authChk          = '인증';
                   } else {
                        obj.authChk          = '미 인증';
                   }
                   this.userList.push(obj);
               });
             }
           }).catch(() => {
                this.$swal('','잠시후 다시 이용해주세요22.','error');
           }).finally(() => {
             this.pageChk = true;
             this.loading = false;
           });
       },
   
       acceptClick(idx, value) {
         if(value=='미 인증'){
             const headers = {
                   'Authorization': 'Bearer ' + sessionStorage.getItem("token")
               }
   
               this.loading = true;
               this.$axios.put(process.env.VUE_APP_AUTH_LIST+"/"+idx+"/accept" ,"",{headers}).then((res) =>{
                 if(res.data.resultCode=="SUCCESS"){
                   this.adminList();
                 }
                 
               }).catch((error) => {
                   this.$swal('',error.response.data.result,'error');
               }).finally(() => {
                 this.loading = false;
               });
         } else {
           this.$swal('','이미 인증된 사용자 입니다.','warning');
         }
       },
   
       reDeleteClick(idx,value) {
         if(value=='인증'){
             const headers = {
                   'Authorization': 'Bearer ' + sessionStorage.getItem("token")
               }
   
               this.loading = true;
               this.$axios.put(process.env.VUE_APP_AUTH_LIST+"/"+idx+"/re-accept" ,"",{headers}).then((res) =>{
                 if(res.data.resultCode=="SUCCESS"){
                   this.adminList();
                 }
                 
               }).catch((error) => {
                   this.$swal('',error.response.data.result,'error');
               }).finally(() => {
                 this.loading = false;
               });
         } else {
           this.$swal('','이미 사용중인 여행사 입니다.','warning');
         }
       },
       sortClcik(value,index) {
           let chkSort = this.formSort.newSort[index];
           if(chkSort=='' || chkSort==null){
               chkSort=0;
           }
   
           const headers = {
                   'Authorization': 'Bearer ' + sessionStorage.getItem("token")
               }
               this.loading = true;
               this.$axios.put(process.env.VUE_APP_TRAVEL_AGENCY_LIST_CRUD+value+"/"+chkSort+"/sort" ,"",{headers}).then((res) =>{
                 if(res.data.resultCode=="SUCCESS"){
                   this.adminList();
                 }
                 
               }).catch((error) => {
                   this.$swal('',error.response.data.result,'error');
               }).finally(() => {
                 this.loading = false;
               });
   
       },
   
       pageCurr(value){
         this.page = value-1;
         this.adminList();
       },
   
       travelSearch(){
          this.page = 0;
          this.pageChk=false;
          this.adminList();
       },
       travelWriteClick() {
         this.$router.push({
           path: "/TravelWrite",
           name: "travelWrite"
         });
       },
   
       todoCclick(value) {
          this.$router.push({
           path: "/TravelDetail",
           name: "travelDetail",
           query: { sn: value }
         });
       },
       updateClick(value){
           this.$router.push({
              path: "/travelModify",
              name: "travelModify",
              query: { sn: value }
            });
       },
   },
   watch : {
     startDate(){
       let splitDate = this.startDate.split("-");
       this.startDateSplit = splitDate[0]+splitDate[1]+splitDate[2];
     },
     endDate(){
       let splitDate = this.endDate.split("-");
       this.endDateSplit = splitDate[0]+splitDate[1]+splitDate[2];
     }
   }
   
   }
   </script>
   <style>
   .custombar{
     width: 50%;
     display: inline-block;
   }
   .deco{
     text-decoration: line-through;
   }
   .curcus{
      cursor: pointer;
   }
   
   .banner {
     background: #a770ef;
     background: -webkit-linear-gradient(to right, #a770ef, #cf8bf3, #fdb99b);
     background: linear-gradient(to right, #a770ef, #cf8bf3, #fdb99b);
   }
   .custombar{
     width: 40%;
     display: inline-block;
   }
   .form-select {
     width : 9%;
     display: inline;
   }
   
   input[type="date"]{
     position: relative;
     min-height:14px;
     border: 1px solid #ddd;
     font-size:16px;
   }
   
   body{margin-top:20px;
   }
   .project-list-table {
       border-collapse: separate;
       border-spacing: 0 12px
   }
   
   .project-list-table tr {
       background-color: #fff
   }
   
   .table-nowrap td, .table-nowrap th {
       white-space: nowrap;
   }
   .table-borderless>:not(caption)>*>* {
       border-bottom-width: 0;
   }
   .table>:not(caption)>*>* {
       padding: 0.75rem 0.75rem;
       background-color: var(--bs-table-bg);
       border-bottom-width: 1px;
       box-shadow: inset 0 0 0 9999px var(--bs-table-accent-bg);
   }
   
   .avatar-sm {
       height: 2rem;
       width: 2rem;
   }
   .rounded-circle {
       border-radius: 50%!important;
   }
   .me-2 {
       margin-right: 0.5rem!important;
   }
   img, svg {
       vertical-align: middle;
   }
   
   a {
       color: #3b76e1;
       text-decoration: none;
   }
   
   .badge-soft-danger {
       color: #f56e6e !important;
       background-color: rgba(245,110,110,.1);
   }
   .badge-soft-success {
       color: #63ad6f !important;
       background-color: rgba(99,173,111,.1);
   }
   
   .badge-soft-primary {
       color: #3b76e1 !important;
       background-color: rgba(59,118,225,.1);
   }
   
   .badge-soft-info {
       color: #57c9eb !important;
       background-color: rgba(87,201,235,.1);
   }
   
   .avatar-title {
       align-items: center;
       background-color: #3b76e1;
       color: #fff;
       display: flex;
       font-weight: 500;
       height: 100%;
       justify-content: center;
       width: 100%;
   }
   .bg-soft-primary {
       background-color: rgba(59,118,225,.25)!important;
   }
   
   </style>
   